header {
  background-color: white;
  border-bottom: 2px solid #000065;
  margin-bottom: 18px;
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.head-container {
  display: flex;
  align-items: center;
}

h1 {
  font-size: 3em;
  margin-left: 16px;
  margin-top: 16px;
  color: #000065;
}

.icons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 5px;
  width: 350px;
}

// .fa-solid {
//   display: none;
// }

img#globe {
  width: 64px;
  height: 64px;
  padding: 10px;
  object-fit: contain;
}

#linkedin, #github {
  width: 52px;
  height: 52px;
  object-fit: contain;
}

@media only screen and (max-width: 500px) {
  #linkedin {
    visibility: hidden;
  }
}

